<script>
// import TableDeviceItem from './Table-device-item.vue';
// import { actionTypes } from '@/store/modules/logs';
import { mapState } from 'vuex';
import TableLogiItem from './TableLogiItem.vue';
// import Calendar from 'v-calendar/lib/components/calendar.umd';
// import DatePicker from 'v-calendar/lib/components/date-picker.umd';

export default {
  name: 'AppTableLogi',
  data() {
    return {
      pageNumber: 0,
      endDateParse: '',
      endDate: '',
      item_device: '',
      pageOfItems: [],
      filterTime: '',
      isFilterButton: false,
      typePackage: '',
      range: {
        start: new Date(2022, 0, 16),
        end: new Date(2022, 0, 17),
      },
      masks: {
        input: 'YYYY-MM-DD h:mm A',
      },
    };
  },
  props: {
    size: {
      type: Number,
      required: false,
      default: 5,
    },
  },
  components: {
    TableLogiItem,
    // Calendar,
    // DatePicker,
  },
  computed: {
    ...mapState({
      isLoading: (state) => state.devices.isLoading,
      logs: (state) => state.logs.logs,
      filterTimeLogs: (state) => state.logs.filterTimeLogs,
    }),
    paginatedData: {
      get() {
        const start = this.pageNumber * this.size,
          end = start + this.size;
        if (this.filterTime && !this.typePackage) {
          return this.filterItem.slice(start, end);
        }
        if (this.typePackage && !this.filterTime) {
          return this.filterCommand.slice(start, end);
        }
        if (this.typePackage && this.filterItem) {
          return this.filterMix.slice(start, end);
        }
        if (this.isFilterButton === true) {
          return this.filterTimeRange.slice(start, end);
        } else return this.logs.slice(start, end);
      },
      set(value) {
        const start = this.pageNumber * this.size,
          end = start + this.size;
        console.log(value, 'value');
        return value.slice(start, end);
      },
    },
    // paginatedData() {
    //   const start = this.pageNumber * this.size,
    //     end = start + this.size;
    //   if (this.filterTime && !this.typePackage) {
    //     return this.filterItem.slice(start, end);
    //   }
    //   if (this.typePackage && !this.filterTime) {
    //     return this.filterCommand.slice(start, end);
    //   }
    //   if (this.typePackage && this.filterItem) {
    //     return this.filterMix.slice(start, end);
    //   } else return this.logs.slice(start, end);
    // },
    pageCount() {
      if (this.filterItem) {
        let l = this.filterItem.length,
          s = this.size;
        return Math.ceil(l / s);
      } else if (this.filterCommand) {
        let l = this.filterCommand.length,
          s = this.size;
        return Math.ceil(l / s);
      } else {
        let l = this.logs.length,
          s = this.size;
        return Math.ceil(l / s);
      }
    },

    filterMix() {
      return this.filterItem.filter((command) => {
        if (this.typePackage === 'any') return true;
        if (command.packageType == this.typePackage) {
          return true;
        } else return false;
      });
    },
    //–î–∏–∞–ø–∞–∑–æ–Ω –¥–∞—Ç
    filterTimeRange() {
      let start = Date.parse(this.range.start);
      let end = Date.parse(this.range.end);
      return this.logs.filter((itemDate) => {
        return (
          new Date(start).toISOString() <= itemDate.localDateTime &&
          itemDate.localDateTime <= new Date(end).toISOString()
        );
      });
    },
    //Select –ø–æ –≤—Ä–µ–º–µ–Ω–∏
    filterItem() {
      if (this.filterTime === 'Two') {
        let startDate = new Date(); //Current time
        let endDate = new Date(startDate.getTime() - 2 * 24 * 60 * 60 * 1000); //Range date Two Day
        let endDateParse = Date.parse(endDate);
        return this.mix(endDateParse);
      } else if (this.filterTime === 'Week') {
        let startDate = new Date();
        let endDate = new Date(startDate.getTime() - 7 * 24 * 60 * 60 * 1000); //Range date One Week
        let endDateParse = Date.parse(endDate);
        return this.mix(endDateParse);
      } else if (this.filterTime === 'Month') {
        let startDate = new Date();
        let endDate = new Date(startDate.getTime() - 31 * 24 * 60 * 60 * 1000); //Range date  One month
        let endDateParse = Date.parse(endDate);
        return this.mix(endDateParse);
      } else if (this.filterTime === 'Hour') {
        let startDate = new Date();
        let endDate = new Date(startDate.getTime() - 60 * 60 * 1000); //Range date One Hour
        let endDateParse = Date.parse(endDate);
        return this.mix(endDateParse);
      } else return false;
    },
    //Select –ø–æ –∫–æ–º–∞–Ω–¥–∞–º
    filterCommand() {
      if (this.typePackage === 'any') return this.logs;
      else if (this.typePackage) {
        return this.filterCommandLog(this.typePackage);
      } else return false;
    },
  },
  methods: {
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
    filterCommandLog(type) {
      return this.logs.filter((command) => {
        if (command.packageType == type) return true;
      });
    },
    mix(end) {
      let startDate = new Date(); //Current time
      let tzoffset = new Date().getTimezoneOffset() * 60000; //offset in milliseconds in timeZone
      let startDateParse = Date.parse(startDate);
      return this.logs.filter((itemDate) => {
        return (
          new Date(end - tzoffset).toISOString() <= itemDate.localDateTime &&
          itemDate.localDateTime <=
            new Date(startDateParse - tzoffset).toISOString()
        );
      });
    },
    filterRange() {
      this.filterTime = '';
      this.typePackage = '';
      this.isFilterButton = true;
      // return (this.paginatedData = this.filterTimeRange);
    },
  },
};
</script>
