import LogsAPi from '@/api/logs.js';

const state = {
  isLoading: false, //–æ–∂–∏–¥–∞–Ω–∏–µ –∑–∞–ø—Ä–æ—Å–∞
  validationErrors: null, //–æ—à–∏–±–∫–∏
  logs: [],
  infoLog: null,
  filterTimeLogs: [],
  filterTypePackage: [],
};

export const mutationTypes = {
  LogsStart: '[logs] LogsStart',
  LogsSuccess: '[logs] LogsSuccess',
  LogsFailure: '[logs] LogsFailure',

  getFilterTimeLogsStart: '[logs] getFilterTimeLogsStart',
  getFilterTimeLogsSuccess: '[logs] getFilterTimeLogsSuccess',
  getFilterTimeLogsFailure: '[logs] getFilterTimeLogsFailure',

  getFilterTypePackageStart: '[logs] getFilterTypePackageStart',
  getFilterTypePackageSuccess: '[logs] getFilterTypePackageSuccess',
  getFilterTypePackageFailure: '[logs] ggetFilterTypePackageFailure',

  getItemLogSuccess: '[logs] getItemLogSuccess',
};
export const actionTypes = {
  getLogs: '[logs] getLogs',
  getItemLog: '[logs] getItemLog',
  getFilterTimeLogs: '[logs] getFilterTimeLogs',
  getFilterTypePackage: '[logs] getFilterTypePackage',
};

const mutations = {
  [mutationTypes.LogsStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.LogsSuccess](state, payload) {
    state.isLoading = false;
    state.logs = payload.data;
  },
  [mutationTypes.LogsFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.getItemLogSuccess](state, payload) {
    state.infoLog = payload;
  },
  [mutationTypes.getFilterTimeLogsStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.getFilterTimeLogsSuccess](state, payload) {
    state.isLoading = false;
    state.filterTimeLogs = payload.data;
  },
  [mutationTypes.getFilterTimeLogsFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },

  [mutationTypes.getFilterTypePackageStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.getFilterTypePackageSuccess](state, payload) {
    state.isLoading = false;
    state.filterTypePackage = payload.data;
  },
  [mutationTypes.getFilterTypePackageFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  [actionTypes.getLogs](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.LogsStart);
      LogsAPi.allLogs(credentials)
        .then((response) => {
          context.commit(mutationTypes.LogsSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.LogsFailure, resp);
          console.log('–æ—à–∏–±–∫–∞', resp);
        });
    });
  },
  [actionTypes.getItemLog](context, credentials) {
    context.commit(mutationTypes.getItemLogSuccess, credentials);
  },
  [actionTypes.getFilterTimeLogs](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getFilterTimeLogsStart);
      LogsAPi.getFilterTimeLogs(credentials)
        .then((response) => {
          context.commit(mutationTypes.getFilterTimeLogsSuccess, response);
          resolve(response);
        })
        .catch((req) => {
          context.commit(mutationTypes.getFilterTimeLogsFailure, req);
          console.log('–æ—à–∏–±–∫–∞', req);
        });
    });
  },
  [actionTypes.getFilterTypePackage](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getFilterTypePackageStart);
      LogsAPi.getFilterTypePackage(credentials)
        .then((response) => {
          context.commit(mutationTypes.getFilterTypePackageSuccess, response);
          resolve(response);
        })
        .catch((req) => {
          context.commit(mutationTypes.getFilterTypePackageFailure, req);
          console.log('–æ—à–∏–±–∫–∞', req);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
