                  @click="$emit('close')"
                  alt=""
                />
              </div>
            </slot>
          </div>
          <div class="modal-body">
            <input
              type="button"
              value="–û—Ç–ø—Ä–∞–≤–∏—Ç—å –∫–æ–º–∞–Ω–¥—É"
              class="modal__btn"
              @click="createNewCommand()"
            />
            <input
              type="button"
              value="–ü–æ–∫–∞–∑–∞—Ç—å –Ω–∞ –∫–∞—Ä—Ç–µ"
              class="modal__btn"
              @click="submitCoordinat()"
            />
            <input
              type="button"
              value="–¢–µ–∫—É—â–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ"
