<script>
import { actionTypes } from '@/store/modules/devices';
import { actionTypesCoordinats } from '@/store/modules/coordinats';
export default {
  name: 'AppModal',
  props: {
    item_device: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  methods: {
    submitCoordinat() {
      this.$store
        .dispatch(actionTypesCoordinats.getCoordinats, this.item_device.serial)
        .then(() => {
          this.$emit('close');
        })
        .catch((er) => {
          console.log('–û—à–∏–±–∫–∞', er);
        });
    },
    createNewCommand() {
      this.$emit('createNewCommand');
    },
    getNewCondition() {
      this.$store
        .dispatch(actionTypes.postCondition, this.item_device.serial)
        .then(() => {
          this.$emit('getNewCondition');
        })
        .catch((er) => {
          console.log('–û—à–∏–±–∫–∞', er);
        });
      // this.$emit('getNewCondition');
    },
  },
};
</script>

