export const getItem = (key) => {
  try {
    return JSON.parse(localStorage.getItem(key)); //–ú–µ—Ç–æ–¥ JSON.parse() —Ä–∞–∑–±–∏—Ä–∞–µ—Ç —Å—Ç—Ä–æ–∫—É JSON, –≤–æ–∑–º–æ–∂–Ω–æ —Å –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ–º –ø–æ–ª—É—á–∞–µ–º–æ–≥–æ –≤ –ø—Ä–æ—Ü–µ—Å—Å–µ —Ä–∞–∑–±–æ—Ä–∞ –∑–Ω–∞—á–µ–Ω–∏—è.
  } catch (e) {
    console.log('Error getting data from localStorage', e);
    return null;
  }
};
//(key, data)–∫—É–¥–∞ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –∏ —á—Ç–æ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º
export const setItem = (key, data) => {
  try {
    localStorage.setItem(key, JSON.stringify(data)); //setItem(–Ω–∞–∑–≤–∞–Ω–∏–µ–ö–ª—é—á–∞, –∑–Ω–∞—á–µ–Ω–∏–µ–ö–ª—é—á–∞)
  } catch (e) {
    console.log('Error saving data from localStorage', e);
  }
};
