    <div class="device__container search-container">
      <div class="search-container__input-serch">
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="–°–µ—Ä–∏–π–Ω—ã–π –Ω–æ–º–µ—Ä"
          v-model="serial"
          aria-label="–í–≤–æ–¥–∏—Ç–µ"
          class="input-search"
        />
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="–¢–µ–ª–µ—Ñ–æ–Ω–Ω—ã–π –Ω–æ–º–µ—Ä"
          v-model="phone"
          aria-label="–í–≤–æ–¥–∏—Ç–µ"
          class="input-search"
        />
        <input
          type="search"
          id="search-bar"
          name="search"
          placeholder="IMEI —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞"
          v-model="imeiBlock"
          aria-label="–í–≤–æ–¥–∏—Ç–µ"
          class="input-search"
        />
      </div>
      <div class="search-container__button-block">
        <div class="search-container__btn">
          <input
            type="button"
            value="–ü–æ–∏—Å–∫"
