            @click="onSubmit()"
          />
        </div>
        <div class="search-container__btn">
          <input
            type="button"
            value="–°–±—Ä–æ—Å–∏—Ç—å"
            class="button btn-primary"
            @click="deleteFilter()"
          />
        </div>
      </div>
    </div>
    <div class="table">
      <!-- {{ this.todosByTitle }} -->
      <table>
        <thead>
          <tr>
            <th></th>
            <th>–°–µ—Ä–∏–π–Ω—ã–π –Ω–æ–º–µ—Ä</th>
            <th>–ù–æ–º–µ—Ä –°–∏–º-–∫–∞—Ä—Ç—ã</th>
            <th>IMEI —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞</th>
            <th>IMEI –°–ò–ú-–∫–∞—Ä—Ç—ã</th>
            <th>–ü–∞—Ä–æ–ª—å</th>
          </tr>
        </thead>

        <tbody>
          <!--    v-for="item in filter_device"-->
          <TableDeviceItem
            v-for="(item, index) in paginatedData"
            :key="item.id"
            v-bind:item_data="item"
            v-bind:idx="index"
            :count="pageNumber"
            @modal-mini="modalMini"
          />
        </tbody>
      </table>

      <!-- <div class="table-error" v-if="paginatedData == 0">
        –î–∞–Ω–Ω—ã—Ö –Ω–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç!
      </div> -->
      <modal-condition
        v-if="showModalCondition"
        v-bind:item_device_command="this.item_device"
        @close="showModalCondition = false"
      />
      <modal-mini
        v-if="showModal"
        v-bind:item_device="this.item_device"
        @close="showModal = false"
        @createNewCommand="createNewCommand"
        @getNewCondition="getNewCondition"
      />
      <modal-command
