  </div>
</template>

<script>
import ModalMini from './Modal-mini.vue';
import TableDeviceItem from './Table-device-item.vue';
import { actionTypes } from '@/store/modules/devices';
import { mapState } from 'vuex';
import ModalCommand from './Modal-command.vue';
import ModalCondition from './Modal-condition.vue';
export default {
  name: 'AppTableDevice',
  data() {
    return {
      showModal: false,
      showModalCommand: false,
      showModalCondition: false,
      serial: '',
      phone: '',
      imeiBlock: '',
      search_block: {},
      filter_device: [],
      pageNumber: 0,
      item_device: '',
    };
  },
  props: {
    size: {
      type: Number,
      required: false,
      default: 10,
    },
  },
  components: { TableDeviceItem, ModalMini, ModalCommand, ModalCondition },
  computed: {
    ...mapState({
      isLoading: (state) => state.devices.isLoading,
      allDevices: (state) => state.devices.allDevices,
      itemDevice: (state) => state.devices.itemDevice,
    }),
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      if (this.itemDevice) {
        return this.itemDevice;
      } else return this.allDevices.slice(start, end);
    },
    pageCount() {
      let l = this.allDevices.length,
        s = this.size;
      return Math.ceil(l / s);
    },
  },
  methods: {
    modalMini(item) {
      this.item_device = '';
      this.item_device = item;
      this.showModal = true;
    },
    //—Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ –≤—Å–µ–º –¥–∞–Ω–Ω—ã–º
    onSubmit() {
      this.search_block = {
        serial: this.serial,
        phone: this.phone,
        imeiBlock: this.imeiBlock,
      };

      this.$store
        .dispatch(actionTypes.postSearch, this.search_block)
        .then(() => {
          this.search_block = {};
          this.serial = '';
          this.phone = '';
          this.imeiBlock = '';
        })
        .catch((er) => {
          console.log('–û—à–∏–±–∫–∞', er);
        });
    },
    //–û—Ç–∫—Ä—ã—Ç–∏–µ –ú–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –ö–æ–º–∞–Ω–¥—ã
    createNewCommand() {
      this.showModal = false;
      this.showModalCommand = true;
    },
    //–û—Ç–∫—Ä—ã—Ç–∏–µ –ú–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –¢–µ–∫—É—â–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
    getNewCondition() {
      this.showModal = false;

      this.showModalCondition = true;
    },
    deleteFilter() {
      this.$store.dispatch(actionTypes.deleteSearch);
      this.serial_search = '';
      this.phone_search = '';
      this.IMEI_search = '';
    },
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
  },

  mounted() {
    this.$store
      .dispatch(actionTypes.getAllDevices)
      .then(() => {})
      .catch((er) => {
        console.log('–û—à–∏–±–∫–∞', er);
      });
  },
};
</script>
