<template>
  <div class="">
    <!-- <button @click="showMap = !showMap">–°–∫—Ä—ã—Ç—å</button> -->
    <l-map
      v-if="this.coordinats == 0"
      style="height: 500px"
      :zoom="zoom"
      :center="center"
    >
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
    </l-map>
    <div class="" v-if="this.coordinats != 0">
      <l-map style="height: 500px" :zoom="zoom" :center="coordinats">
        <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
        <l-layer-group ref="features">
          <l-popup>
            <div class="">–ö–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã: {{ this.coordinats }}</div>
            <span> {{ this.place.display_name }}</span></l-popup
          >
        </l-layer-group>
        <l-marker :lat-lng="this.coordinats" :icon="icon" @click="openPopUp()">
        </l-marker>
      </l-map>
    </div>
  </div>
</template>

<script>
import { LMap, LTileLayer, LLayerGroup, LMarker, LPopup } from 'vue2-leaflet';
import { icon } from 'leaflet';
import { mapState } from 'vuex';
import axios from 'axios';

export default {
  components: {
    LMap,
    LTileLayer,
    LLayerGroup,

    LMarker,
    LPopup,
  },
  data() {
    return {
      showMap: true,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 15,

      center: [54.965008, 82.94634],
      markerLatLng: [54.965008, 82.94634],
      icon: icon({
        iconUrl: require('../assets/img/icon2.png'),
        iconSize: [40, 40],
        iconAnchor: [16, 37],
      }),
      searchCoord: {},
      place: '',
    };
  },
  computed: {
    ...mapState({
      coordinats: (state) => state.coordinats.coordinats,
    }),
  },
  methods: {
    openPopUp() {
      this.searchCoord = {
        lat: this.coordinats[0],
        lon: this.coordinats[1],
      };
      axios
        .get(
          `https://nominatim.openstreetmap.org/reverse?format=json&lat=${this.searchCoord.lat}&lon=${this.searchCoord.lon}&zoom=18&addressdetails=1`
        )
        .then((response) => {
          this.place = response.data;
          this.$refs.features.mapObject.openPopup(this.coordinats);
          console.log(this.$refs.features);
        })
        .catch(() => {
          this.place = '–ú–µ—Å—Ç–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ';
        });
    },
  },
};
</script>
