import DevicesAPi from '@/api/devices.js';

// import { getItem } from '@/helpers/persistanceStorage';

const state = {
  isLoading: false, //–æ–∂–∏–¥–∞–Ω–∏–µ –∑–∞–ø—Ä–æ—Å–∞
  validationErrors: null, //–æ—à–∏–±–∫–∏
  itemDevice: '',
  allDevices: [],
  —Åondition: [],
};

export const mutationTypes = {
  allDevicesStart: '[devices] allDevicesStart',
  allDevicesSuccess: '[devices] allDevicesSuccess',
  allDevicesFailure: '[devices] allDevicesFailure',

  postCommandStart: '[devices] postCommandStart',
  postCommandSuccess: '[devices] postCommandSuccess',
  postCommandFailure: '[devices] postCommandFailure',

  postSearchStart: '[devices] postSearchStart',
  postSearchSuccess: '[devices] postSearchSuccess',
  postSearchFailure: '[devices] postSearchFailure',
  //—É–¥–∞–ª–µ–Ω–∏–µ –ø–æ–∏—Å–∫–∞
  deleteSearchStart: '[devices] deleteSearchStart',
  //–ø–æ–ª—É—á–µ–Ω–∏–µ —Ç–µ–∫—É—â–µ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è
  postConditionStart: '[devices] postConditionStart',
  postConditionSuccess: '[devices] postConditionSuccess',
  postConditionFailure: '[devices] postConditionFailure',
};
export const actionTypes = {
  getAllDevices: '[devices] devices',
  postCommand: '[devices] command',
  postSearch: '[devices] search',
  deleteSearch: '[devices] delete',
  postCondition: '[devices] condition',
};

const mutations = {
  [mutationTypes.allDevicesStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.allDevicesSuccess](state, payload) {
    state.isLoading = false;
    state.allDevices = payload.data;
  },
  [mutationTypes.allDevicesFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.postCommandStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.postCommandSuccess](state) {
    state.isLoading = false;
  },
  [mutationTypes.postCommandFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.postSearchStart](state) {
    state.isLoading = true;
    state.validationErrors = null;
  },
  [mutationTypes.postSearchSuccess](state, payload) {
    state.isLoading = false;
    state.itemDevice = payload.data;
  },
  [mutationTypes.postSearchFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.deleteSearchStart](state) {
    state.itemDevice = '';
  },
  [mutationTypes.postConditionStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.postConditionSuccess](state, payload) {
    state.isLoading = false;
    state.—Åondition = payload.data;
  },
  [mutationTypes.postConditionFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  [actionTypes.getAllDevices](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.allDevicesStart);
      DevicesAPi.allDevices()
        .then((response) => {
          context.commit(mutationTypes.allDevicesSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.allDevicesFailure, resp);
          console.log('–æ—à–∏–±–∫–∞', resp);
        });
    });
  },
  [actionTypes.postCommand](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.postCommandStart);
      DevicesAPi.postCommand(credentials)
        .then((response) => {
          context.commit(mutationTypes.postCommandSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.postCommandFailure, resp);
          console.log('–æ—à–∏–±–∫–∞', resp);
        });
    });
  },
  [actionTypes.postSearch](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.postSearchStart);
      DevicesAPi.postSearch(credentials)
        .then((response) => {
          context.commit(mutationTypes.postSearchSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.postSearchFailure, resp);
          console.log('–æ—à–∏–±–∫–∞', resp);
        });
    });
  },
  [actionTypes.deleteSearch](context) {
    context.commit(mutationTypes.deleteSearchStart);
  },
  [actionTypes.postCondition](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.postConditionStart);
      DevicesAPi.postCondition(credentials)
        .then((response) => {
          context.commit(mutationTypes.postConditionSuccess, response);
          resolve(response);
        })
        .catch((resp) => {
          context.commit(mutationTypes.postConditionFailure, resp);
          console.log('–æ—à–∏–±–∫–∞', resp);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
