import axios from '@/api/axios';

const allLogs = (credentials) => {
  return axios.get('/info/logs', {
    params: {
      serial: credentials,
    },
  });
};
const getFilterTimeLogs = (credentials) => {
  return axios.get('/info/search/time/logs', {
    params: {
      time: credentials,
    },
  });
};
const getFilterTypePackage = (credentials) => {
  return axios.get('/info/search/time/logs', {
    params: {
      type: credentials,
    },
  });
};

export default {
  allLogs,
  getFilterTimeLogs,
  getFilterTypePackage,
};
